import React, { useState } from 'react';
import { FaChevronDown, FaChevronUp } from 'react-icons/fa'; // Ensure you have react-icons installed

const FAQ = () => {
  const [openFAQ, setOpenFAQ] = useState(null);

  const toggleFAQ = (index) => {
    setOpenFAQ(openFAQ === index ? null : index);
  };

  const faqs = [
    {
      question: "What is SQL Database Management?",
      answer: "SQL (Structured Query Language) is a domain-specific language used for managing relational databases. It allows users to query, insert, update, and delete data in a database. SQL is essential for managing data and performing various operations on relational databases such as MySQL, PostgreSQL, Oracle, and SQL Server.",
    },
    {
      question: "What are the main components of SQL?",
      answer: "SQL consists of several components, including Data Definition Language (DDL) for defining database schema, Data Manipulation Language (DML) for manipulating data, Data Control Language (DCL) for controlling access and permissions, and Transaction Control Language (TCL) for managing transactions.",
    },
    {
      question: "Why is SQL important in database management?",
      answer: "SQL is important in database management because it provides a standardized way to interact with relational databases. It allows users to retrieve and manipulate data efficiently, ensure data integrity through constraints, manage user permissions, and optimize database performance with indexing and query optimization techniques.",
    },
    {
      question: "What are the different types of SQL commands?",
      answer: "SQL commands are categorized into several types: DDL commands (CREATE, ALTER, DROP) for database schema operations, DML commands (SELECT, INSERT, UPDATE, DELETE) for data manipulation, DCL commands (GRANT, REVOKE) for access control, and TCL commands (COMMIT, ROLLBACK) for transaction management.",
    },
    {
      question: "How does SQL handle data integrity?",
      answer: "SQL ensures data integrity through constraints such as primary keys, foreign keys, unique constraints, and check constraints. These constraints enforce rules on data values, preventing invalid data from being inserted or updated in the database.",
    },
    {
      question: "What are the advantages of using SQL?",
      answer: "SQL offers several advantages, including ease of use, portability across different database systems, scalability for handling large datasets, robustness with ACID properties (Atomicity, Consistency, Isolation, Durability), and extensive community support and documentation.",
    },
    {
      question: "What are the common SQL database management systems (DBMS) used?",
      answer: "Common SQL database management systems include MySQL, PostgreSQL, Oracle Database, Microsoft SQL Server, SQLite, and IBM Db2. These DBMSs vary in features, scalability, and suitability for different applications and enterprise needs.",
    },
    {
      question: "How can SQL be used in application development?",
      answer: "SQL is used in application development for tasks such as database design and schema creation, data manipulation and querying, implementing business logic through stored procedures and triggers, ensuring data security and integrity, and integrating databases with applications through APIs.",
    },
    {
      question: "What career opportunities are available for SQL professionals?",
      answer: "SQL professionals can pursue various career paths, including Database Administrator (DBA), SQL Developer, Data Analyst, Business Intelligence (BI) Developer, Data Engineer, and Software Engineer. SQL skills are highly valued in industries such as finance, healthcare, e-commerce, and IT services.",
    },
  ];

  return (
    <div className="container mx-auto mt-16">
      <h2 className="text-center text-xl md:text-3xl font-bold mb-10 leading-tight">
        Frequently Asked Questions about SQL Database Management
      </h2>
      <div className="flex flex-col gap-4">
        {faqs.map((faq, index) => (
          <section key={index} className="border-b pb-2">
            <div
              className="flex justify-between items-center cursor-pointer"
              onClick={() => toggleFAQ(index)}
            >
              <h3 className="font-semibold text-lg">{faq.question}</h3>
              {openFAQ === index ? <FaChevronUp /> : <FaChevronDown />}
            </div>
            {openFAQ === index && <p className="mt-2">{faq.answer}</p>}
          </section>
        ))}
      </div>
    </div>
  );
};

export default FAQ;
