import React, { useState } from 'react';
import JavaImg from '../../../../assets/Java.webp';

const ModuleContent = ({ moduleNumber, moduleTitle, moduleDescription, lessons, imgSrc }) => {
  const [isContentVisible, setContentVisible] = useState(false);

  const handleToggle = () => {
    setContentVisible(!isContentVisible);
  };

  return (
    <div className="flex flex-col lg:flex-row border border-grey-500 bg-grey-200 bg-opacity-75 select-none cursor-pointer">
      <div className="flex items-center justify-between lg:hidden px-4 py-2" onClick={handleToggle}>
        <p className="text-orange font-semibold">{moduleNumber}</p>
        <svg
          xmlns="http://www.w3.org/2000/svg"
          width={35}
          height={24}
          viewBox="0 0 24 24"
          fill="none"
          stroke="currentColor"
          strokeWidth={2}
          strokeLinecap="round"
          strokeLinejoin="round"
          className={`text-grey transition-transform duration-300 ${isContentVisible ? 'rotate-180' : ''}`}
        >
          <polyline points="6 9 12 15 18 9" />
        </svg>
      </div>
      <div className="lg:flex py-4 lg:pl-5 pr-3 lg:pr-0 lg:space-x-5 lg:items-start">
        <div className="flex flex-col lg:items-center lg:flex-shrink-0">
          <div className="w-20 md:p-0 hidden lg:block">
            <img
              src={imgSrc}
              className="hidden lg:block"
              alt={moduleTitle}
              width="80px"
              height="80px"
            />
          </div>
          <p className="hidden lg:block text-orange font-semibold">{moduleNumber}</p>
        </div>
        <div className="flex flex-col lg:w-full">
          <div className="flex flex-col text-grey space-y-3 leading-tight">
            <div className="flex lg:flex-row flex-col items-start lg:items-center space-y-1 lg:space-y-0 lg:space-x-2">
              <h3 className="text-black font-semibold text-lg">{moduleTitle}</h3>
            </div>
            <div className="relative flex flex-col items-start">
              <div className="overflow-hidden" style={{ height: "auto" }}>
                {moduleDescription}
              </div>
            </div>
          </div>
          <div
            className={`flex flex-col transition-all duration-1000 overflow-hidden ${isContentVisible ? 'max-h-screen' : 'max-h-0'}`}
          >
            <div className="flex flex-col mt-5">
              {lessons.map((lesson, index) => (
                <div key={index} className="py-4 flex space-x-3 border-t border-grey-400 lessonContainer">
                  <div className="flex flex-col items-start space-y-3 leading-tight">
                    <div className="flex lg:flex-row flex-col lg:items-center">
                      <p className="text-lg font-semibold text-grey">{lesson.title}</p>
                    </div>
                    <p>{lesson.description}</p>
                  </div>
                </div>
              ))}
            </div>
          </div>
        </div>
        <div className="hidden lg:flex pt-10 pr-5 order" onClick={handleToggle}>
          <svg
            xmlns="http://www.w3.org/2000/svg"
            width={35}
            height={24}
            viewBox="0 0 24 24"
            fill="none"
            stroke="currentColor"
            strokeWidth={2}
            strokeLinecap="round"
            strokeLinejoin="round"
            className={`text-grey transition-transform duration-300 ${isContentVisible ? 'rotate-180' : ''}`}
          >
            <polyline points="6 9 12 15 18 9" />
          </svg>
        </div>
      </div>
    </div>
  );
};

const ModuleList = () => {
  const modules = [
    {
      moduleNumber: 'Module 1',
      moduleTitle: 'Introduction to Databases and SQL',
      moduleDescription:
        'Understand the basics of databases, SQL, and relational database management systems (RDBMS).',
      imgSrc: JavaImg,
      lessons: [
        { title: 'Lesson 1: Introduction to Databases', description: 'Explore the fundamentals of databases and their importance in modern applications.' },
        { title: 'Lesson 2: Overview of SQL', description: 'Learn about Structured Query Language (SQL) and its role in managing relational databases.' },
        { title: 'Lesson 3: Relational Database Concepts', description: 'Understand the concepts of tables, rows, columns, keys, and relationships in relational databases.' },
        { title: 'Lesson 4: Setting Up SQL Environment', description: 'Practical guide on setting up a SQL environment for learning and development.' },
      ],
    },
    {
      moduleNumber: 'Module 2',
      moduleTitle: 'SQL Data Manipulation and Queries',
      moduleDescription:
        'Learn to query data using SELECT statements and perform data manipulation with INSERT, UPDATE, and DELETE statements.',
      imgSrc: JavaImg,
      lessons: [
        { title: 'Lesson 1: SELECT Statement', description: 'Master the SELECT statement to retrieve data from single or multiple tables.' },
        { title: 'Lesson 2: Data Filtering and Sorting', description: 'Use WHERE clause for data filtering and ORDER BY clause for sorting results in SQL queries.' },
        { title: 'Lesson 3: Data Modification', description: 'Learn to modify data in tables using INSERT, UPDATE, and DELETE statements.' },
        { title: 'Lesson 4: Transactions and Concurrency', description: 'Understand transaction management and concurrency control in SQL databases.' },
      ],
    },
    {
      moduleNumber: 'Module 3',
      moduleTitle: 'Advanced SQL Techniques',
      moduleDescription:
        'Explore advanced SQL queries, including JOINs, subqueries, and aggregations.',
      imgSrc: JavaImg,
      lessons: [
        { title: 'Lesson 1: JOIN Operations', description: 'Master INNER JOIN, LEFT JOIN, RIGHT JOIN, and FULL JOIN operations for combining data from multiple tables.' },
        { title: 'Lesson 2: Subqueries and Derived Tables', description: 'Use subqueries and derived tables to perform complex queries and solve advanced data retrieval tasks.' },
        { title: 'Lesson 3: Aggregations and Grouping', description: 'Learn to use aggregate functions like COUNT, SUM, AVG, MIN, and MAX for data summarization.' },
        { title: 'Lesson 4: SQL Views and Indexes', description: 'Understand the benefits of SQL views and indexes for optimizing query performance and data access.' },
      ],
    },
  ];

  return (
    <div>
      {modules.map((module, index) => (
        <ModuleContent
          key={index}
          moduleNumber={module.moduleNumber}
          moduleTitle={module.moduleTitle}
          moduleDescription={module.moduleDescription}
          lessons={module.lessons}
          imgSrc={module.imgSrc}
        />
      ))}
    </div>
  );
};

export default ModuleList;
