import React, { useState } from 'react';

const CourseTop = () => {
    const [isExpanded, setIsExpanded] = useState(false);

    const toggleExpansion = () => {
        setIsExpanded(!isExpanded);
    };

    return (
        <div className="flex flex-col text-grey justify-center">
            <h2 className="text-2xl font-bold mb-4 leading-tight text-center">
                What is SQL Database Management Training?
            </h2>
            <div
                className="text-lg overflow-hidden transition-all duration-700"
                style={{ maxHeight: isExpanded ? 'none' : '80px' }}
            >
                <p className="text-justify">
                    SQL Database Management training focuses on mastering SQL fundamentals, database design, and management. SQL (Structured Query Language) is essential for interacting with relational database management systems (RDBMS) such as MySQL, PostgreSQL, Oracle, and SQL Server.
                </p>
                <p className="text-justify mt-4">
                    This course covers topics including data querying, data manipulation, database normalization, transactions, indexing, and stored procedures. Students will learn to design efficient database schemas, optimize SQL queries, and ensure data integrity.
                </p>
                <p className="text-justify mt-4">
                    Practical exercises and projects will reinforce concepts in database administration, performance tuning, and troubleshooting. SQL Database Management training also prepares learners to handle large datasets, implement security measures, and integrate databases into applications.
                </p>
                <p className="text-justify mt-4">
                    By the end of the course, participants will be equipped with the skills necessary for database administration, SQL development, and data-driven application development roles across various industries.
                </p>
            </div>
            <button
                className="underline text-left mt-3 focus:outline-none hover:text-orange self-center"
                onClick={toggleExpansion}
            >
                {isExpanded ? 'Show less' : 'Show more'}
            </button>
        </div>
    );
};

export default CourseTop;
